@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css");
@import url("https://fonts.googleapis.com/css?family=Kurale:400|Bellefair:400|Inter:400");
/* The following line is used to measure usage of this code in production. For more info see our usage billing page */
@import url("https://px.animaapp.com/639467befeb1ffb72de92546.639467c0e34c735154ecd625.MH2RNtq.rcp.png");

@font-face {
  font-family: "Lao Muang Khong-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("static/fonts/LaoMuangKhong-Regular.ttf") format("truetype");
}


.screen a {
  display: contents;
  text-decoration: none;
}

.container-center-horizontal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  pointer-events: none;
  width: 100%;
}

.container-center-horizontal > * {
  flex-shrink: 0;
  pointer-events: auto;
}

* {
  box-sizing: border-box;
}
